import React, { Component } from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import {
  closeChannel,
  depositChannel,
  pollChannels,
  putChannel
} from "../../actions/channelsActions";
import PollingContainer from "../../genericContainers/PollingContainer";
import "react-toastify/dist/ReactToastify.css";
import { quickPayment } from "../../actions/paymentsActions";
import ChannelList from "../../components/channels/ChannelList";
import FiltersChannels from "../../components/filtersChannels/FiltersChannels";
import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";
import {
  attributeToType,
  SORT_ASC,
  SORT_BY_NAME,
  SORT_DESC
} from "../../constants/uiConstants";
import { andLikeFilter } from "../../lib/filter/filters";
import { basicSort } from "../../lib/sort/basicSorting";
import { TOKEN } from "../../lib/search/searchConstants";

import client from "../../restClient";

import { mapSuggestions, getTaskDetail } from "../../actions/actionUtils";
import SearchSuggestion from "../../components/search/SearchSuggestion";
import NumberFormat from "react-number-format";
import { pollTokens } from "../../actions/tokensActions";
import { incrementTaskPending } from "../../actions/taskActions";
import { getName } from "../../lib/tokens/tokensLogic";
import { selectedSuggestion } from "../../actions/searchActions";

class ChannelsContainer extends Component {
  constructor(props, context) {
    super(props, context);
    this.state = {
      show: false,
      inputAddress: "",
      inputToken: "",
      inputAmount: "",
      inputSettleTimeOut: 500,
      inputAmountPayment: "",
      filterTokenName: undefined,
      filterPartnerAddress: undefined,
      sortBy: SORT_BY_NAME,
      partnerSuggestions: [],
      tokensSuggestions: []
    };

    this.handleShow = this.handleShow.bind(this);
    this.handleClose = this.handleClose.bind(this);
    this.handleChangeAddress = this.handleChangeAddress.bind(this);
    this.handleChangeToken = this.handleChangeToken.bind(this);
    this.handleChangeAmount = this.handleChangeAmount.bind(this);
    this.handleDepositChannel = this.handleDepositChannel.bind(this);
  }

  handleClose() {
    this.setState({ show: false });
  }

  handleShow() {
    this.setState({ show: true });
  }

  handleChangeAddress(event) {
    this.setState({ inputAddress: event.target.value });
  }

  handleChangeToken(event) {
    this.setState({ inputToken: event.target.value });
  }

  handleChangeAmount(event) {
    this.setState({ inputAmount: event.value });
  }

  handleOpenChannel = () => {
    this.props.incrementTaskPending(getTaskDetail("openChannel", this.state));
    this.props.putChannel(
      this.state.inputAddress,
      this.state.inputToken,
      this.state.inputAmount,
      this.state.inputSettleTimeOut
    );

    this.handleClose();
  };

  handleCloseChannel = (tokenAddress, partnerAddress) => {
    this.props.closeChannel(tokenAddress, partnerAddress);
  };

  handleDepositChannel = (
    tokenAddress,
    partnerAddress,
    totalDeposit,
    balance
  ) => {
    this.props.depositChannel(
      tokenAddress,
      partnerAddress,
      totalDeposit,
      balance
    );
  };

  handleQuickPayments = (partnerAddress, tokenAddress, totalDeposit) => {
    this.props.quickPayment(partnerAddress, tokenAddress, totalDeposit);
  };

  handleFilterTokenNameChange = event =>
    this.setState({ filterTokenName: event.target.value });

  handleFilterPartnerAddressChange = event =>
    this.setState({ filterPartnerAddress: event.target.value });

  handleSortByChange = event => this.setState({ sortBy: event.target.value });

  filterChannels = () => {
    if (
      this.props.channels &&
      this.props.channels.length > 0 &&
      (this.state.filterTokenName || this.state.filterPartnerAddress)
    ) {
      return andLikeFilter(
        this.props.channels,
        "token_address",
        "partner_address",
        this.state.filterTokenName,
        this.state.filterPartnerAddress
      );
    } else {
      return this.props.channels;
    }
  };

  applyFiltersAndSorting = () => {
    let filteredChannels = this.filterChannels();
    const attType = attributeToType[this.state.sortBy];
    if (filteredChannels) {
      if (attType === String) {
        return basicSort(
          filteredChannels,
          this.state.sortBy,
          attributeToType[this.state.sortBy],
          SORT_ASC
        );
      } else {
        return basicSort(
          filteredChannels,
          this.state.sortBy,
          attributeToType[this.state.sortBy],
          SORT_DESC
        );
      }
    }
  };

  getTokensSuggestions = value => {
    if (value != null && value !== "") {
      let suggestions = [];
      if (this.props.tokens && this.props.tokens.length > 0) {
        for (let i = 0; i < this.props.tokens.length; i++) {
          if (
            this.props.tokens[i].address
              .toLowerCase()
              .includes(value.toLowerCase())
          ) {
            suggestions.push({
              value: this.props.tokens[i].address,
              type: TOKEN,
              label:
                getName(this.props.tokens[i].address, this.props.tokens) +
                " - " +
                this.props.tokens[i].address
            });
          }
        }
      }
      this.setState({ tokensSuggestions: suggestions });
    }
  };
  fetchPaymentSuggestions = value => {
    client
      .get("/api/v1/searchLumino", {
        params: {
          query: value,
          only_receivers: true
        }
      })
      .then(response => {
        if (response) {
          let mappedResponse = mapSuggestions(response.data);
          this.setState({ partnerSuggestions: mappedResponse });
        }
      })
      .catch(error => {
        console.log(JSON.stringify(error));
      });
  };

  onPartnerSelected = (event, { suggestion }) => {
    this.setState({ inputAddress: suggestion.value });
  };

  onTokenSelected = (event, { suggestion }) => {
    this.setState({ inputToken: suggestion.value });
  };

  onPartnerSuggestionsClearRequested = () => {
    this.setState({
      partnerSuggestions: []
    });
  };

  onTokenSuggestionsClearRequested = () => {
    this.setState({
      tokensSuggestions: []
    });
  };

  getData = () => {
    this.props.pollChannels();
    this.props.pollTokens();
  };

  render = () => {
    return (
      <div>
        <div className="py-2 px-5 filters-bar">
          <FiltersChannels
            onTokenFilterChange={this.handleFilterTokenNameChange}
            onPartnerFilterChange={this.handleFilterPartnerAddressChange}
            onSortByChange={this.handleSortByChange}
          />
        </div>
        <PollingContainer
          render={this.renderPolling}
          pollAction={this.getData}
          dueTim={0}
          periodOfScheduler={2000}
        />

        <div className="container-fluid px-5">
          <div className="row my-3">
            <div className="col text-right">
              <button
                type="button"
                name="button"
                className="btn btn-lg btn-green"
                onClick={this.handleShow}
              >
                New Channel{" "}
                <i className="fal fa-chart-network fa-lg ml-2 pl-2 border-left align-middle" />
              </button>
            </div>
          </div>
        </div>
        <div className="container-fluid px-5">
          <ChannelList
            incrementTaskPending={this.props.incrementTaskPending}
            channelList={this.applyFiltersAndSorting()}
            closeChannel={(a, b) => this.handleCloseChannel(a, b)}
            depositChannel={this.handleDepositChannel}
            payChannel={(a, b, c) => this.handleQuickPayments(a, b, c)}
            tokens={this.props.tokens}
            selectedSuggestion={this.props.selectedSuggestion}
          />
        </div>

        <Modal show={this.state.show} onHide={this.handleClose} centered>
          <Modal.Header closeButton>
            <Modal.Title>Create New Channel</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <div className="form-group">
              <SearchSuggestion
                placeholder={"Partner address"}
                suggestions={this.state.partnerSuggestions}
                value={this.state.inputAddress}
                onSuggestionsFetchRequested={this.fetchPaymentSuggestions}
                onSuggestionsClearRequested={
                  this.onPartnerSuggestionsClearRequested
                }
                onChange={this.handleChangeAddress}
                onSuggestionSelected={this.onPartnerSelected}
              />
            </div>
            <div className="form-group">
              <SearchSuggestion
                placeholder={"Token address"}
                suggestions={this.state.tokensSuggestions}
                value={this.state.inputToken}
                onSuggestionsFetchRequested={this.getTokensSuggestions}
                onSuggestionsClearRequested={
                  this.onTokenSuggestionsClearRequested
                }
                onChange={this.handleChangeToken}
                onSuggestionSelected={this.onTokenSelected}
              />
            </div>
            <div className="form-group">
              <NumberFormat
                value={this.state.inputAmount}
                className="form-control shadow-none"
                placeholder={"Amount"}
                onValueChange={e => this.handleChangeAmount(e)}
              />
            </div>
          </Modal.Body>
          <Modal.Footer>
            <Button
              className="btn-pay border-0 btn-block py-3"
              variant="primary"
              onClick={this.handleOpenChannel}
            >
              Create
            </Button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  };

  renderPolling = () => {
    return null; // Maybe in a future we can add a toast or something
  };
}

const mapStateToProps = state => {
  return {
    channels: state.channelReducer.channels,
    channelsChanged: state.channelReducer.channelsChanged,
    tokens: state.tokenReducer.tokens
  };
};

const mapDispatchToProps = dispatch => {
  const actions = {
    pollChannels: pollChannels,
    putChannel: putChannel,
    incrementTaskPending: incrementTaskPending,
    closeChannel: closeChannel,
    depositChannel: depositChannel,
    quickPayment: quickPayment,
    pollTokens: pollTokens,
    selectedSuggestion: selectedSuggestion
  };
  return bindActionCreators(actions, dispatch);
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ChannelsContainer);
